<template>
  <div class="partnerCen">
    <div class="partnerCen_top" :style="background">
      <div class="top_one">
        <div class="one_left">
          <img src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fpic4.zhimg.com%2F50%2Fv2-791b5947478755503749ff6675a52366_hd.jpg&refer=http%3A%2F%2Fpic4.zhimg.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1631064507&t=1aa27869e124f99bcda371dda31eaedb" alt="" />
          <span>Hi，张三</span>
        </div>
        <div class="one_right">
          <img src="@/assets/images/partner/rule.png" alt="" />
          <div class="right_text">规则</div>
        </div>
      </div>
      <div class="top_two">总用户(个)</div>
      <div class="top_three">999</div>
      <div class="top_five"><span>其他用户&ensp;</span>220个 ></div>
      <div class="top_fl">
        <div class="fl_cen">
          <div class="cen_top">今日新增(个)</div>
          <div class="cen_fl">120</div>
        </div>
        <div class="fl_line"></div>
        <div class="fl_cen">
          <div class="cen_top">本月新增(个)</div>
          <div class="cen_fl">600</div>
        </div>
      </div>
    </div>
    <div class="partnerCen_item">
      <!-- 选择盒子 -->
      <div class="item_line">
        <div class="line_children" 
        :class="userID==1?'line_bule3':''"
        :style="{'--bgurl': userID==1 ? ('url('+image.partner_left+')'): ''}"
         @click="_changePageSwitchID(1)"
        >直接用户</div>
        <div class="line_children" 
        :class="userID==2?'line_bule':''"
        :style="{'--bgurl': userID==2 ? ('url('+image.partner_right+')'): ''}"
        @click="_changePageSwitchID(2)"
        >间接用户</div> 
      </div>
      <!-- 选择盒子 end-->
      <div class="item_box">
        <div class="box_chilreds">
          <div class="chilreds_line" 
          v-for="(item,index) in levelView" 
          :key="index">
          <!-- 排序图标 start -->
            <div class="line_left" v-show="!item.otherUser">
              <template v-if="item.id<4">
                <div class="left_box"
                :style="{'--Level': ('url('+_setlevelImage(item.id)+')')}"
                ></div>
              </template>
               <template v-else>
                <div>{{item.id}}</div>
              </template>
            </div>
          <!-- 排序图标 end -->
            <div class="line_right">
              <img src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fc-ssl.duitang.com%2Fuploads%2Fitem%2F202005%2F20%2F20200520024521_zkkfs.jpg&refer=http%3A%2F%2Fc-ssl.duitang.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1630743604&t=e9b40d9c747b377df5ae2174a080b8ff" alt="" />
              <div class="right_box">
                <div class="box_one">
                  <div>用户{{item.userNum}}</div>
                  <div v-show="item.isLevel" class="one_img" :style="{'--Ima': ('url('+image.partner+')')}">合伙人</div>
                </div>
                <div class="box_two">累计贡献金额 <span>{{item.profit}}</span> 元</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import partner_left from '@/assets/images/partner/partner_left.png';
import partner_cen from '@/assets/images/partner/partner_cen.png';
import partner_right from '@/assets/images/partner/partner_right.png';
import level1 from '@/assets/images/partner/icon_jin.png';
import level2 from '@/assets/images/partner/icon_yin.png';
import level3 from '@/assets/images/partner/icon_tong.png';
import partner from '@/assets/images/partner/partner.png';
export default {
    name:'partnerCen',
    data(){
      return {
          userID:1,
          levelView:[
            {id:1,userNum:'45656478',profit:334,isLevel:true,otherUser:false},
            {id:2,userNum:'45656478',profit:334,isLevel:false,otherUser:false},
            {id:3,userNum:'45656478',profit:334,isLevel:true,otherUser:false},
            {id:4,userNum:'45656478',profit:334,isLevel:false,otherUser:false},
            {id:5,userNum:'45656478',profit:334,isLevel:false,otherUser:false},
            {id:6,userNum:'45656478',profit:334,isLevel:true,otherUser:false},
            {id:7,userNum:'45656478',profit:334,isLevel:true,otherUser:false},
            {id:8,userNum:'45656478',profit:334,isLevel:false,otherUser:false},
          ],
          image:{
            partner_left:partner_left,
            partner_cen:partner_cen,
            partner_right:partner_right,
            level1:level1,
            level2:level2,
            level3:level3,
            partner:partner
          },
          background:{
            backgroundImage:'url(' + require('@/assets/images/partner/partner_back.png') + ')',
            backgroundRepeat:'no-repeat',
            backgroundSize: '100% 100%'
        }
      }
    },
    methods:{
      _changePageSwitchID(id){
        let { userID,levelView } = this
        if( userID==id ) return ;
        this.userID=id
        if(id==3){
          for(let i=0;i<levelView.length;i++){
            levelView[i].otherUser=true
          }
          this.levelView=levelView
        }else{
           for(let i=0;i<levelView.length;i++){
            levelView[i].otherUser=false
          }
          this.levelView=levelView
        }
      },
      _setlevelImage(id){//设置图标
        const { level1,level2,level3 } = this.image
        if(id==1) return level1;
        if(id==2) return level2;
        if(id==3) return level3;
      }
    }
    
}
</script>

<style lang="scss">
    @import './index.scss';
</style>


