import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index/Index'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',//首页
      name: 'index',
      component: Index,
      meta:{index:1,keepAlive: true }
    },
    {
      path: '/orderState',//订单状态
      name: 'orderState',
      component: ()=>import('@/pages/orderState/Index'),
      meta:{index:2,keepAlive: false }
    },
    {
      path: '/detailed',//明细
      name: 'detailed',
      component: ()=>import('@/pages/detailed/Index'),
      meta:{index:2,keepAlive: false }
    },
    {
      path: '/moneyIndex',//提现首页
      name: 'moneyIndex',
      component: ()=>import('@/pages/Withdrawal/index/Index'),
      meta:{index:2,keepAlive: false }
    },
    {
      path: '/moneyAdd',//添加银行卡
      name: 'moneyAdd',
      component: ()=>import('@/pages/Withdrawal/add/Index'),
      meta:{index:4,keepAlive: false }
    },
    {
      path: '/moneySelect',//选择银行卡
      name: 'moneySelect',
      component: ()=>import('@/pages/Withdrawal/select/Index'),
      meta:{index:3,keepAlive: false }
    },
  ]
})



